<template>
  <div>
    <Hero class="is-clipped is-fullheight">
      <b-dropdown
        slot="header-links"
        class="navbar-item header-dropdown fade-in"
      >
        <label
          slot="trigger"
          :for="($refs.headerNf || {}).nameId"
          class="button is-danger has-text-weight-bold"
        >Newsletter</label>
        <b-dropdown-item
          custom
          class="is-paddingless"
        >
          <NewsletterForm
            ref="headerNf"
            style="width: 20em"
          />
        </b-dropdown-item>
      </b-dropdown>

      <div class="columns is-vcentered">
        <div class="column is-6">
          <div class="joiner is-size-4 is-hidden-tablet" />
          <h1 class="fancy-text title is-1 is-size-4-mobile has-text-centered-mobile fade-in-down delay-point-25">
            JavaScript drag and drop, resizing, and multi-touch gestures
            for modern browsers (and&nbsp;also&nbsp;IE9+)
            <VersionBadge
              class="fade-in delay-point-25"
              style="vertical-align: super"
            />
          </h1>

          <Features class="content is-hidden-mobile fade-in delay-point-50" />
        </div>

        <div class="column is-6 anim-column">
          <div class="joiner is-double is-size-2 is-hidden-tablet" />
          <Animations
            ref="animations"
            @hook:mounted="startAnimations"
          />
        </div>
      </div>
    </Hero>

    <div class="joiner is-size-1 is-size-4-mobile is-double" />

    <Features class="box--mobile is-hidden-tablet fade-in-up delay-point-50">
      <div class="fancy-text title is-4 is-hidden-tablet has-text-centered-mobile has-text-primary">
        Features
      </div>
    </Features>

    <div class="container">
      <div class="box--mobile fade-in-up delay-point-50">
        <div class="title has-text-centered has-text-primary">
          How does it work?
        </div>

        <div class="columns is-vcentered">
          <div class="column">
            <Intro class="box is-shadowless content is-size-5" />
          </div>
          <div class="column is-half">
            <div>
              <LiveDemo
                class="box is-shadowless"
                is-demo-only
                :demo-html="require('@/demos/rainbow-pixel-canvas.html')"
              >
                <p
                  slot="title"
                  class="title is-4 has-text-dark"
                >
                  Here's a simple pixel drawing app with interact.js
                </p>
              </LiveDemo>
            </div>
          </div>
        </div>
      </div>

      <br>

      <LiveDemo
        v-for="demo of demos"
        :key="demo"
        :demo-html="demo"
        class="box--mobile"
      />
    </div>

    <Footer class="footer--margined" />

    <ScrollToTop />
  </div>
</template>

<script>
import Vue from 'vue'

import introMd from '../content/home-intro.md'
import { getMdVue, timeout, pageLoaded, getVersion } from '../utils'

const Intro = getMdVue(introMd)

export default Vue.extend({
  components: { Intro },
  data () {
    return {
      demos: [],
      showScrollIcon: false
    }
  },
  metaInfo: {
    title: 'interact.js - JavaScript drag and drop, resizing and multi-touch gestures for modern browsers',
    titleTemplate: null
  },
  mounted () {
    import(/* webpackChunkName: "home-demos" */ '../homeDemos').then(({ default: demos }) => {
      this.demos = demos
    })
  },

  destroyed () {
    window.removeEventListener('scroll', this.onScroll)
  },
  methods: {
    startAnimations () {
      const { animations } = this.$refs

      pageLoaded.then(async () => {
        const version = await getVersion('version').catch(() => '')
        const importUrl = version
          ? `https://cdn.interactjs.io/v${version}/interactjs/index.js`
          : 'https://cdn.jsdelivr.net/npm/@interactjs/interactjs/index.js'

        const scriptOpen = '<script type="module">'
        const importFrom = `\nimport interact from \n'${importUrl}'`
        const draggable = `\n\ninteract('.item').draggable({
  onmove(event) {
    console.log(event.pageX,
                event.pageY)
  }
})`
        // eslint-disable-next-line no-useless-escape
        const scriptClose = '\n<\/script>'
        animations.updateCodeFrameSize(scriptOpen + importFrom + draggable + scriptClose)

        await timeout(500)

        animations.start({
          '': async () => {
            await animations.codeDemo({ code: scriptOpen, lang: 'html', append: true, delimiter: 'character' })
            await animations.codeDemo({ code: importFrom, lang: 'javascript', append: true, delimiter: 'character' })
            await timeout(200)
            await animations.codeDemo({ code: draggable, lang: 'javascript', append: true })
            await animations.codeDemo({ code: scriptClose, lang: 'html', append: true, delimiter: 'character' })
          }
        })
      })
    }
  }
})
</script>
